import React from 'react';
import { graphql } from 'gatsby';
import Hero from '../components/layout/Hero';
import Button from '../components/Button';
import Layout from "../components/layout";

const IndexPage = ({data}) => (
  <Layout>
  <div>
    <Hero title="Cryptocurrency Resources" />
    <div className="main">
      <div className="contain">
      <p className="lead">A curated list of the best high-quality cryptocurrency resources.</p>
        <ul>
          {data.allLinksJson.edges.map((node, i) => (
            <li key={node.node.id} 
            className={"col-md-6"+ 
                        ((i+1)%2 === 0 ? '--last': '') + " col-lg-4" + 
                        ((i+1)%3 === 0 ? '--last': '') + " button-" +i
            }>
              <Button url={node.node.page} className="home-button">
                <h2>{node.node.title}</h2>
                <p>{node.node.lead}</p>
              </Button>
            </li>
          ))}
        </ul>
      </div>
    </div>
  </div>
  </Layout>
);

export const indexQuery = graphql`
query IndexQuery{
  allLinksJson {
    edges {
      node {
        id
        page
        title
        lead
      }
    }
  }
}
`

export default IndexPage;
